body {
  font-family: "Roboto", sans-serif;
  $color1: #94b49f;
  $color2: #cee5d0;
  $color3: #fcf8e8;
  $color4: #ecb390;

  background: linear-gradient(to bottom, $color1, $color2, $color3, $color4);

  .movie-content {
    width: 75%;
    height: auto;
  }

  h1 {
    color: blueviolet;
  }
  .change {
    background-color: yellow;
    padding: 10px;
  }
  .autoprefixer {
    -webkit-user-select: none;
       -moz-user-select: none;
            user-select: none;
  }
}

/* Dodatkowe niestandardowe style */
.login-form-container {
  position: fixed;
  top: 0;
  right: -300px;
  height: 100%;
  width: 300px;
  background-color: #f8f9fa;
  transition: right 0.3s ease;

  &.show {
    right: 0;
  }
}
.pagination.pagination-sm.pagination-secondary .page-item .page-link {
  color: #6c757d; /* Kolor tekstu nieaktywnych stron */
  border-color: transparent; /* Brak obramowania dla nieaktywnych stron */
  background-color: transparent; /* Przezroczyste tło dla nieaktywnych stron */
}

.pagination.pagination-sm.pagination-secondary .page-item.active .page-link {
  background-color: #6c757d;
  border-color: #6c757d;
}

.pagination.pagination-sm.pagination-secondary .page-link {
  color: #6c757d;
}

.pagination.pagination-sm.pagination-secondary .page-link:hover {
  color: #343a40;
}

.pagination.pagination-sm.pagination-secondary .page-item.active .page-link {
  color: #fff; /* Kolor tekstu aktywnej strony */
  background-color: #343a40; /* Kolor tła aktywnej strony */
  border-color: #343a40; /* Kolor obramowania aktywnej strony */
}


